@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400,700);
@import url(https://fonts.googleapis.com/earlyaccess/notosanskr.css);

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	vertical-align: baseline;
	font-size: 100%;
	font: inherit;
	border: 0;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
* {
	box-sizing: border-box;
}
img {
	vertical-align: top;
}
a {
	text-decoration: none;
	color: #000;
}
body {
	line-height: 1;
	font-family: "Roboto", "Noto Sans KR", sans-serif;
	font-size: 1em;
	font-weight: 400;
}
input[type=text],
input[type=password],
input[type=address],
input[type=reset],
input[type=submit],
textarea {
	font-family: "Roboto", "Noto Sans KR", sans-serif;
}

/* header */
#header .upper {
	min-width: 1100px;
	height: 40px;
	background-color: #f1f1f1;
}
#header .upper .upper_inner {
	margin: 0 auto;
	width: 1100px;
}
#header .upper .upper_inner .utils {
	float: right;
	margin-top: 8px;
}
#header .upper .upper_inner .utils li {
	display: inline-block;
	padding-left: 6px;
}
#header .upper .upper_inner .utils li a {
	line-height: 24px;
	font-size: 13px;
	color: #777;
}
#header .upper .upper_inner li a.mypage {
	display: block;
	width: 65px;
	height: 22px;
	line-height: 22px;
	text-align: center;
	font-size: 12px;
	letter-spacing: 0;
	background: url(./ico/myecc_bg.png) 0 0 no-repeat;
	color: #fff;
}
#header .menu_zone {
	position: absolute;
	z-index: 1000;
	min-width: 1100px;
	width: 100%;
	background-color: #fff;
}
#header .menu_zone::after {
	content: "";
	display: block;
	position: absolute;
	top: 66px;
	width: 100%;
	height: 1px;
	background-color: rgba(0,0,0,.08);
}
#header .menu_zone .menu_zone_inner {
	position: relative;
	margin: 0 auto;
	width: 1100px;
	text-align: center;
}
.menu_zone .menu_zone_inner h1 {
	position: absolute;
	left: 0;
	top: -20px;
	z-index: 100;
	width: 108px;
	height: 108px;
}
.menu_zone .menu_zone_inner .bi {
	position: absolute;
	right: 0;
	top: 17px;
	width: 66px;
	height: 37px;
}
#GNB {
	margin-left: 50px;
	height: 67px;
	overflow: hidden;
}
/*
#GNB:hover {
	height: 300px;
}
*/
#GNB > ul > li {
	display: inline-block;
	position: relative;
	margin-left: 80px;
	vertical-align: top;
}
#GNB > ul > li:first-child {
	margin-left: 0;
}
#GNB > ul > li > a {
	display: block;
	position: relative;
	padding: 25px 10px;
	font-size: 17px;
	color: #333;
}
#GNB > ul > li > a::after {
	content: "";
	display: none;
	position: absolute;
	left: 0;
	bottom: 0;
	z-index: 100;
	width: 100%;
	height: 2px;
	background-color: #ff9800;
}
#GNB > ul > li:hover > a::after,
#GNB > ul > li.active > a::after{
	display: block;
}
#GNB .sub {
	position: absolute;
	left: 0;
	margin-left: -40%;
	margin-top: 20px;
	width: 180%;
}
#GNB .sub li a {
	display: block;
	padding: 8px;
	text-decoration: none;
	font-size: 15px;
	color: #444;
}
#GNB .sub li a:hover {
	color: #ff9800;
}
